import React, { useMemo } from "react";
import CustomModal from "../Modal/customModal";
import {
  AutoSizer,
  List,
  CellMeasurer,
  CellMeasurerCache,
} from "react-virtualized";
import "react-virtualized/styles.css"; // Make sure to import the styles
import "./SuccessModal.css"; // We'll create this file for custom styling
import ReusableAlert from "../Alert/ReusableAlert";
import { handleExportExcel } from "../../utils/helpers"; // Import the export function

const SuccessModal = ({
  show = false,
  onHide = () => {},
  responseData = [],
}) => {
  // Create a cache for cell measurements
  const cache = useMemo(() => {
    return new CellMeasurerCache({
      fixedWidth: true,
      defaultHeight: 50,
    });
  }, [responseData.length]);

  // These are our column definitions
  const columns = [
    { key: "name", header: "Name", width: "40%" },
    { key: "revision", header: "Revision", width: "10%" },
    { key: "status", header: "Status", width: "15%" },
    { key: "message", header: "Message", width: "35%" },
  ];

  // Handle export to Excel
  const handleExport = () => {
    handleExportExcel(responseData, "upload-results.xlsx");
  };

  // Reset cache when data changes
  React.useEffect(() => {
    cache.clearAll();
  }, [responseData, cache]);

  // Render an individual row
  const rowRenderer = ({ key, index, style, parent }) => {
    const item = responseData[index];

    return (
      <CellMeasurer
        key={key}
        cache={cache}
        columnIndex={0}
        rowIndex={index}
        parent={parent}
      >
        <div style={style} className="virtualized-row">
          <div
            className="virtualized-cell"
            style={{ width: columns[0].width }}
            title={item.name}
          >
            {item.name}
          </div>
          <div
            className="virtualized-cell"
            style={{ width: columns[1].width }}
            title={item.revision}
          >
            {item.revision}
          </div>
          <div
            className={`virtualized-cell status-cell ${item.status.toLowerCase()}`}
            style={{ width: columns[2].width }}
            title={item.status}
          >
            {item.status}
          </div>
          <div
            className="virtualized-cell"
            style={{ width: columns[3].width }}
            title={item.message}
          >
            {item.message}
          </div>
        </div>
      </CellMeasurer>
    );
  };

  return (
    <CustomModal
      show={show}
      onHide={onHide}
      title="Upload Results"
      size="lg"
      footerButtons={[
        {
          label: "Close",
          variant: "danger",
          onClick: onHide,
        },

        {
          label: "Export to Excel",
          variant: "success",
          onClick: handleExport,
        },
      ]}
    >
      <div className="success-modal-content">
        <ReusableAlert
          variant="success"
          message={
            <>
              ‚úÖ <strong>Success:</strong> {responseData.length} item
              {responseData.length > 1 ? "s" : ""} processed successfully!
            </>
          }
          className="mb-3"
        />

        <div className="virtualized-table-container">
          {/* Table Header */}
          <div className="virtualized-header">
            {columns.map((column) => (
              <div
                key={column.key}
                className="virtualized-header-cell"
                style={{ width: column.width }}
              >
                {column.header}
              </div>
            ))}
          </div>

          {/* Table Body with Virtualized List */}
          <div className="virtualized-body">
            <AutoSizer>
              {({ height, width }) => (
                <List
                  width={width}
                  height={height - 10} // Adjust to account for header
                  rowCount={responseData.length}
                  rowHeight={cache.rowHeight}
                  deferredMeasurementCache={cache}
                  rowRenderer={rowRenderer}
                  overscanRowCount={5}
                />
              )}
            </AutoSizer>
          </div>
        </div>
      </div>
    </CustomModal>
  );
};

export default SuccessModal;
